.redline {
  display: flex;
  position: absolute;
  align-items: center;
  width: 20vh;
  z-index: 2;
  &__ball {
      position: relative;
      bottom: 6px;
      right: 6px;
      height: 12px;
      width: 12px;
      border-radius: 100%;
      background-color: red;
  }
  &__line {
      position: relative;
      bottom: 6px;
      right: 6px;
      width: 100%;
      border-top: 2px solid red;
  }
}